import React from "react"
import { Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"

const Contact = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title
  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="Contact" />
      <div>
        <div className="mb-4 text-lg font-bold py-6">
          <h1>Contact</h1>
        </div>
        <div>
          <div className="text-xs">
            I am a data scientist, specialised in optimizing solutions based on
            empirical data. I have experience in various machine learning
            methods aimed at creating predictive data and data mining excerises.
          </div>
          <div className="text-xs pt-5">
            I have experience in delivering valuable insight through data
            science technique and implement action oriented solutions to complex
            business problems.
          </div>
          <div className="text-xs pt-5">
            If you have any comments, ideas, critiques, or you just want to say
            hi, don't hesitate to send me an email at{" "}
            <span className="text-indigo-600">vv18859ds@gmail.com</span>!
          </div>
        </div>
      </div>
      <div>
        <div className="text-lg font-bold py-6">
          <h1>Around the web</h1>
        </div>
        <div>
          <ol>
            <li className="font-bold text-xs">
              <small className="text-sm">Email:</small>
              <span className="text-indigo-600">
                <a mailto="vv18859ds@gmail.com"> vv18859ds@gmail.com</a>
              </span>
            </li>
            <li className=" font-bold text-xs">
              <small className="text-sm">Github:</small>{" "}
              <a
                className="text-indigo-600"
                href="https://www.github.com/varadharajanviji"
              >
                varadharajanviji
              </a>
            </li>
            <li className="font-bold text-xs">
              <small className="text-sm">LinkedIn:</small>
              <a
                className="text-indigo-600"
                href="https://www.linkedin.com/in/viji-varadharajan"
              >
                viji-varadharajan
              </a>
            </li>
            <li className="font-bold text-xs">
              <small className="text-sm">kaggle:</small>
              <a
                className="text-indigo-600"
                href="https://www.kaggle.com/varadharajanviji"
              >
                {" "}
                varadharajanviji{" "}
              </a>
            </li>
          </ol>
        </div>
      </div>
    </Layout>
  )
}

export default Contact

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`
